<script>
    export let title;
    export let content;
    export let severity;
</script>


<div class="notification">
    <div class="icon {severity === "INFO" ? "info" : severity === "ERROR" ? "error" : "success"}"></div>
    <div class="title">{title}</div>
    <div class="content">{content}</div>
</div>

<style>
    .notification {
        font-family: "Montserrat", sans-serif;
        display: grid;
        grid-template-areas: 
            "a b"
            "a c";
        grid-template-columns: max-content 1fr;
        column-gap: 10px;
        background: rgba(0, 0, 0, 0.68);
        border-radius: 6px;
        width: 300px;
        overflow: hidden;
        padding: 10px;
    }

    .icon {
        height: 40px;
        width: 40px;
        background-position: center;
        background-repeat: no-repeat;
        border-radius: 4px;
    }

    .icon.success {
        background-color: #4DAC68;
        background-image: url("../img/notification/success.svg");
    }

    .icon.error {
        background-color: #FC4130;
        background-image: url("../img/notification/error.svg");
    }

    .icon.info {
        background-color: #4677FF;
        background-image: url("../img/notification/info.svg");
    }

    .icon {
        grid-area: a;
    }

    .title {
        grid-area: b;
        font-size: 14px;
        color: white;
        font-weight: 600;
    }

    .content {
        grid-area: c;
        font-size: 12px;
        color: #CBD1E3;
    }
</style>